.DetailedRecipe {
    display: grid;
    grid-template-rows: 2% 40% 55% 2%;
    grid-template-columns: 2% 96% 2%;
    grid-template-areas: 
        ".   .   ."
        "calories photo checkbox"
        ".  desc  ."
        ".   .   .";
    width: 100vw;
    height: 85vh;
    text-align: center;
}

.RecipePhoto {
    grid-area: photo;
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.DrinkDetails {
    grid-area: desc;
    display: flex;
    flex-direction: column;
}

.DrinkName {
    font-weight: 500;
    margin-top: 0;
    margin-bottom: 0;
}

.Hide {
    text-align:left;
    overflow-y: scroll;
    width: calc(var(100% - 30px));
    height: 500px;
    padding-left: 30px;

}

.RemoveMargins {
    margin-top: 0;
    margin-bottom: 0;
}

.Selector {
    grid-area: checkbox;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    transform: translateX(-50px);
}

.Checkbox {
    width: 40px;
    height: 40px
}

.CalorieHolder {
    grid-area: calories;
    margin-top: 0;
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    transform: translateX(11px);
}

.Label {
    padding-left: 2px;
}

.hideScrollBar::-webkit-scrollbar {
    display: none;
}
  
/* Hide scrollbar for IE, Edge and Firefox */
.hideScrollBar {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

@media only screen and (min-width: 768px) {
    p {
        font-size: x-large;
    }

    .DetailedRecipe {
        display: grid;
        grid-template-rows: 2% 40% 55% 2%;
        grid-template-columns: 2% 96% 2%;
        grid-template-areas: 
            ".   .   ."
            "calories photo checkbox"
            ".  desc  ."
            ".   .   .";
        width: 50vw;
        height: 90vh;
        text-align: center;
    }
}